import {
		mapState,  
	    mapMutations  
	} from 'vuex'; 
export default {
	data() {
		return {
			name: "",
			sex: 1,
			version: "",
		}
	},
	onLoad() {
		// console.log(this.globalUserInfo)
	},
	onShow() {
		var that = this;
		if(that.token !== null){
			uni.request({
				url: this.global_baseUrl + '/user_info/',
				data: {
					
				},
				header: {
					'Authorization': 'Token  ' + this.token,
				},
				success: (res) => {
					this.name = res.data.name;
					this.sex = res.data.sex;
				}
			});
		}
		uni.request({
			url: this.global_baseUrl + '/appletRelated/',
			data: {
				
			},
			success: (res) => {
				this.version = this.res.data[0].versionNumber;
			}
		});
		
	},
	computed: {
		...mapState(['token', 'globalUserInfo'])
	},
	methods: {
		showTip() {
			uni.showModal({
				 title: '提示',
				 content: '该功能尚未开发',
				 showCancel: false,
			});
		},
		logIn() {
			uni.reLaunch({
				url: '/pages/login/login'
			});
		},
		goToPage(url) {
			if (this.token==null){
				uni.showToast({
					title: "登录才能使用此功能",
					icon: 'none'
				})
				return false;
			}
			if (!url) return;
			uni.navigateTo({
				url
			});
		},
		signOut() {
			uni.reLaunch({
			    url: '/pages/login/login'
			});
		}
	}
}
